import styled from "styled-components";
import gClef from "../../assets/img/g-clef.png";
import wholeNote from "../../assets/img/whole-note.png";

const Wrapper = styled.div`
  position: relative;
  margin-top: 40px;
  margin-bottom: 40px;
`;

const Staff = styled.div`
  width: 500px;
`;

const Line = styled.div`
  height: 1px;
  margin-bottom: 14px;
  background-color: #000;
`;

const Gclef = styled.img`
  position: absolute;
  top: -25px;
  left: 0;
  height: 116px;
`;

const Notes = styled.div`

`;

const WholeNote = styled.img`
  position: absolute;
  top: 0;
  left: 0;
  height: 14px;
`;

export default class MusicStaff extends React.Component {
  render() {
    return (
      <Wrapper>
        <Notes>
          <WholeNote src={wholeNote} />
        </Notes>
        <Gclef src={gClef} />
        <Staff>
          <Line />
          <Line />
          <Line />
          <Line />
          <Line />
        </Staff>
      </Wrapper>
    );
  }
}
